body {
  background-color: beige;
}

#header {
  display: flex;
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 400;
  background-color: beige;
  color: #7ad47a;
  height: 150px;
  margin-top: 50px;
  font-size: 24px;
  border: 1px solid #67d367;
  border-radius: 20px;
  height: 200px;
  box-shadow: grey;
  margin-bottom: 100px;
}

.main-title h1 {
  margin-top: 80px;
}

.select-aircraft {
  display: flex;
  margin-top: 50px;
  margin-left: 10px;
  justify-content: space-around;
  width: 600px;
  font-family: Arial, Helvetica, sans-serif;
}

.optionClass {
  width: 150px;
  border: 1px solid #67d367;
  border-radius: 30px;
  height: 60px;
  font-size: 18px;
}

.select-aircraft button {
  width: 150px;
  border: 1px solid #67d367;
  border-radius: 30px;
  height: 60px;
  font-size: 18px;
}

.selected-ordered-services {
  display: flex;
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 100px;
}

.select-option {
  margin-left: 30px;
  margin-right: 30px;
}

.aircraft-pic {
  width: 70px;
  height: 70px;
  background-image: url("./assets/3423.jpg");
  background-size: cover;
  background-position-x: -15px;
  margin-left: 10px;
  margin-right: 10px;
  margin-top: 10px;
}

.services-main {
  display: flex;
  align-items: center;
  padding: 0 30px;
}

.services-main p:last-child {
  margin-left: 10px;
  font-weight: 600;
}

